import styled from 'styled-components'

const Tag = styled.span`
    float: left;
    background: lightblue;
    color: white;
    border-radius: 5px;
    padding: 5px;
    margin: 0 5px 5px 0;
    letter-spacing: 1px;
    cursor: pointer;
`;

export default Tag;


