import styled from 'styled-components'

const Wrapper = styled.div`
  background: white;
  box-shadow: 3px 3px 10px rgba(0,0,0, 0.1);
  font-family: 'Hind', sans-serif;
  font-weight: 400;
  border-radius: 20px;
  padding: 10px;
  font-size: large;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
`;

export default Wrapper;


